#category-container {
    background-color: black;
    width: 100vw;
    height: 100vh;
    color: #fff;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    // padding: 2vw;
}

.left-category {
    flex: 1 0 0;

    .left-container {
        width: fit-content;
        margin: 8vw auto 0;

        h2 {
            color: #72db73;
            font-family: "Single Day", cursive;
            font-size: 2.5vw;
            font-weight: 400;
            margin-bottom: 4vw;
            width: fit-content;
        }

        h1 {
            font-family: "Roboto";
            font-size: 2.3vw;
            width: 15vw;
            line-height: 3.5vw;
        }
    }
}

.right-category {
    flex: 1.5 0 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.category-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
    gap: 2vw;
}

.movie-container {
    border-radius: 10px;
    font-family: "DM sans";
    font-size: 0.8vmax;
    font-weight: 400;
    letter-spacing: 1px;
    padding: 0.3vw;

    img {
        height: 7vw;
        width: 11vw;
        object-fit: contain;
    }
}

.selected-category {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 10px;
    width: 15vw;
    margin: 2vw auto;
}

.selected-type {
    display: flex;
    justify-content: space-around;
    background-color: #148a08;
    border-radius: 50px;
    width: 7vw;
    padding: 0.2vw 0.5vw;

    span {
        font-family: "Roboto";
        font-size: 0.9vw;
        font-weight: 400;
        padding: 0.2vw 1vw;
    }

    button {
        border: 0;
        outline: none;
        font-size: 1vw;
        color: #085c00;
        background: none;
        cursor: pointer;
    }
}

.warning {
    margin-top: 5vw;
    color: #ff0000;
    font-family: "Roboto";
    font-size: 1rem;

    img {
        height: 20px;
        width: 20px;
    }
}

.next {
    background-color: #148a08;
    font-family: "DM sans";
    text-transform: capitalize;
    color: #fff;
    font-size: 1vw;
    border: 0;
    outline: none;
    border-radius: 50px;
    padding: 0.2vw 1.5vw;
    margin: 0 0 0 auto;
    grid-column: 3/4;
}

.movie-container.selected {
    outline: 4px solid green;
}